<template>
  <div class="layout">
    <Layout>
      <Header>
        <Menu mode="horizontal" theme="dark" active-name="1">
          <div >
            <MenuItem name="1" @click.native="account()">
              <Icon type="ios-navigate"></Icon>
              管理用户
            </MenuItem>
            <MenuItem name="2" @click.native="inventory()">
              <Icon type="ios-keypad"></Icon>
              管理库存
            </MenuItem>
            <MenuItem name="3" @click.native="coupon()">
              <Icon type="ios-analytics"></Icon>
              管理优惠券
            </MenuItem>
            <MenuItem name="4" @click.native="order()">
              <Icon type="ios-paper"></Icon>
              管理订单
            </MenuItem>
            <MenuItem style="float: right;">
              <RadioGroup v-model="dataSource">
                <Radio :label="0">Our Hive</Radio>
                <Radio :label="1">Real Hive</Radio>
              </RadioGroup>
            </MenuItem>
          </div>
        </Menu>
      </Header>
      <Content style="padding: '0 50px';margin-top: 20px;">
        <Card>
          <div style="min-height: 600px;">
            <router-view
            @createOrder='createOrder'
            :newOrderList='newOrderList'
            :dataSource='dataSource'
            @clearNewOrderList='clearNewOrderList'></router-view>
          </div>
        </Card>
      </Content>
      <Footer class="layout-footer-center">1999-2019 &copy; Cloud Computing</Footer>
    </Layout>
  </div>
</template>

<script>
export default {
  data(){
    return {
      newOrderList: [],
      dataSource: 1
    }
  },
  methods: {
    clearNewOrderList(){
      this.newOrderList = []
    },
    createOrder(arg){
      var temp = {
        order_id: arg.order_id,
        buyer_account_id: arg.buyer_account_id,
        good_name: arg.good_name,
        size: arg.size,
        produce_place: arg.produce_place,
        amount: arg.amount,
        year: arg.year,
        month: arg.month,
        day: arg.day,
        price: arg.price,
        receive_address: arg.receive_address
      }
      this.newOrderList.push(temp)
    },
    account () {
      console.log('jump to Account.vue')
      this.$router.push('account')
    },
    inventory(){
      console.log('jump to Inventory.vue')
      this.$router.push('inventory')
    },
    coupon(){
      console.log('jump to Coupon.vue')
      this.$router.push('coupon')
    },
    order(){
      console.log('jump to Order.vue')
      this.$router.push('order')
    }
  }
}
</script>

<style>
  .layout {
    border: 1px solid #d7dde4;
    background: #f5f7f9;
    position: relative;
    border-radius: 4px;
    overflow: hidden;
  }

  .layout-logo {
    width: 100px;
    height: 30px;
    background: #5b6270;
    border-radius: 3px;
    float: left;
    position: relative;
    top: 15px;
    left: 20px;
  }

  .layout-nav {
    width: 420px;
    margin: 0 auto;
    margin-right: 20px;
  }

  .layout-footer-center {
    text-align: center;
  }
</style>



